.submit {
  max-width: 100%;
  justify-content: center;
  align-items: center;
  height: auto;
}

.imageBox {
  position: relative;
}

.buttonBox {
  /* width: 100%; */
  position: absolute;
  top: 10px;
  left: 10px;
  display: flex;
  flex-direction: row;
  size: 300px;
  justify-content: space-around;
  align-items: stretch;
}

.gotoApp {
  background-color: #fd79a8;
  font-size: 30px;
  /* border: groove; */
  border-radius: 10px;
  padding: 10px;
}

.exit {
  font-size: 15px;
  border-radius: 5px;
  padding: 10px;
  margin-left: 5px;
  border: hidden;
}
